import {Component, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {SessionService} from '../session.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  public person: { email: string, password: string };

  constructor(private http: HttpClient, public session: SessionService) {
    this.person = {email: '', password: ''};
  }

  ngOnInit() {
  }

  public async login() {
    const loginResult = await this.http.post(this.session.apiUrl + 'api/Person/login', this.person).toPromise();
    console.log('loginResult: ', loginResult);
    this.session.setToken(loginResult);
  }
}
